import { __decorate } from "tslib";
import { Component, Vue, Prop } from 'vue-property-decorator';
import HelloWorld from '@/components/HelloWorld.vue'; // @ is an alias to /src
let Home = class Home extends Vue {
    constructor() {
        super(...arguments);
        // 声明周期钩子
        this.model = { details: '', type: 'type1' };
        this.houseId = '';
        // tab切换配置
        this.selectedLabelDefault = 'unprocessed';
        this.tabBarList = [{
                label: '未处理',
                value: 'unprocessed'
            }, {
                label: '已处理',
                value: 'processed'
            }];
        // 
        this.page = 1;
        this.isShowMore = true;
        this.repairList = [];
    }
    activated() {
        // this.getWeiXinConfig()
        this.pageInit();
    }
    validateHandler(result) {
        this.validity = result.validity;
        this.valid = result.valid;
        console.log(result);
        console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex);
    }
    getBindingInfo() {
        let query = this.$route.query;
        if (query.code) {
            const toast = this['$createToast']({
                time: 0,
                txt: '数据获取中...'
            });
            toast.show();
            this.$store.dispatch('getCodeInfo', { code: query.code }).then((response) => {
                if (response.success) {
                    if (response.data.openid) {
                        this.openid = response.data.openid;
                        this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } });
                    }
                    else if (response.data.sessionId) {
                        this.$store.commit('SET_SESSION_ID', response.data.sessionId);
                        this.$store.commit('SET_USER_INFO', response.data.userInfo);
                        this.tabBarChange();
                    }
                }
                toast.hide();
            });
        }
        else {
            console.log('不是微信登陆');
        }
    }
    pageInit() {
        let query = this.$route.query;
        if (this.$store.state.sessionId) {
            this.tabBarChange();
        }
        else if (query.code && !this.$store.state.sessionId) {
            this.getBindingInfo();
        }
    }
    tabBarChange(value) {
        this.page = 1;
        this.repairList = [];
        this.isShowMore = true;
        this.getRepairList();
    }
    get houseOptions() {
        this.$store.state.houseList.forEach((obj) => {
            obj.value = obj.id;
            obj.text = obj.account_info[0].name + ' ' + obj.build_info[0].name + ' ' + obj.unitNum + '单元' + obj.floorNum + '层' + obj.roomNum + '室';
        });
        return this.$store.state.houseList;
    }
    get userInfo() {
        return this.$store.state.userInfo;
    }
    getHouseInfo() {
        let info = {};
        this.$store.state.houseList.forEach((obj) => {
            if (obj.id === this.houseId) {
                info = obj;
            }
        });
        return info;
    }
    getRepairList() {
        const toast = this['$createToast']({
            time: 0,
            txt: '数据获取中...'
        });
        toast.show();
        this.$store.dispatch('getRepairList', { page: this.page, type: this.selectedLabelDefault }).then((response) => {
            if (response.success) {
                if (response.data.list.length > 0) {
                    this.repairList = this.repairList.concat(response.data.list);
                }
                else {
                    this.isShowMore = false;
                }
            }
            toast.hide();
        });
    }
    showMoreClick() {
        this.page++;
        this.getRepairList();
    }
    updateRepairState(id) {
        this.$store.dispatch('updateRepairState', { id: id }).then((response) => {
            if (response.success) {
                this['$createDialog']({
                    type: 'alert',
                    content: '处理完成',
                    icon: 'cubeic-ok'
                }).show();
                this.tabBarChange();
            }
        });
    }
};
__decorate([
    Prop({ default: 'default value' })
], Home.prototype, "propB", void 0);
Home = __decorate([
    Component({
        components: {
            HelloWorld
        },
        filters: {
            getFormatDate(date) {
                return new Date(date).getFullYear() + '年' + (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '号';
            },
            getRepairType(type) {
                if (type === 'type1') {
                    return '开锁服务';
                }
                else if (type === 'type2') {
                    return '公共报修';
                }
                else if (type === 'type3') {
                    return '个人报修';
                }
                else {
                    return '其他报修';
                }
            }
        }
    })
], Home);
export default Home;
